<template lang="pug">
    div.flex-container.flex-centered.full-width
        brakeSVG.flex-item-row
        steerSVG.flex-item-row
        
</template>

<script>
import brakeSVG from "../assets/images_src/brake.svg";
import steerSVG from "../assets/images_src/steer.svg";
import { TimelineMax, TweenMax } from "gsap";

export default {
  components: {
    brakeSVG,
    steerSVG
  },
  props: {
    step: {
      type: Number,
      default: 1
    }
  },
  watch: {
    step: function(val) {
      if (this.$options.timelines !== []) {
        switch (val) {
          case 2:
            this.displaySVGs();
            break;
          default:
            this.hideSVGs();
            break;
        }
      }
    }
  },
  mounted() {
    this.svg_target = [
      document.getElementById("brake"),
      document.getElementById("steer")
    ];
    var brake_duration = 1;
    var delay_pedal = 0.3;
    var steer_duration = 2;
    var steer_magnitude = 30;
    var response_color = "#ea8050";
    /* Initial states */
    // Brake
    TweenMax.set(this.svg_target, { alpha: 0 });
    TweenMax.set("#brake_pedal", {
      rotation: "37deg_ccw",
      svgOrigin: "75.229073 35.830265"
    });
    TweenMax.set("#brake_arm", { rotation: -70, transformOrigin: ".8px 1px" });
    TweenMax.set("#shoe", {
      transformOrigin: "left bottom",
      x: 5,
      y: -5,
      rotation: -45
    });
    TweenMax.set("#leg", { transformOrigin: "middle bottom", rotation: -5 });
    // Steer
    TweenMax.set("#steering_wheel", { transformOrigin: "center" });

    /* Timeline */
    // Brake
    const tl_brake = new TimelineMax({ repeat: 1, yoyo: true });
    tl_brake.add("start", 0);
    tl_brake
      .to("#shoe", brake_duration, { rotation: -35, x: 13, y: 5 }, "start")
      .to("#leg", brake_duration, { rotation: -15 }, "start")
      .to(
        "#brake_pedal",
        brake_duration - delay_pedal,
        { rotation: "15deg_ccw", delay: delay_pedal, repeatDelay: delay_pedal },
        "start"
      )
      .to(
        "#brake_arm",
        brake_duration - delay_pedal,
        { rotation: -50, delay: delay_pedal, repeatDelay: delay_pedal },
        "start"
      )
      .to(
        "#brake .bg",
        brake_duration - delay_pedal,
        {
          fill: response_color,
          delay: delay_pedal,
          repeatDelay: delay_pedal,
          ease: Expo.easeOut
        },
        "start"
      );
    tl_brake.pause().progress(0);

    // Steer
    const tl_steer = new TimelineMax({ yoyo: true });
    tl_steer.add("start", 0);
    tl_steer.add("anchor1", steer_duration / 4);
    tl_steer.add("anchor2", (steer_duration * 3) / 8);
    tl_steer.add("anchor3", (steer_duration * 5) / 8);
    tl_steer
      .to(
        "#steering_wheel",
        steer_duration / 4,
        { rotation: -steer_magnitude, ease: Sine.easeInOut },
        "start"
      )
      .to("#steering_wheel", steer_duration / 4, {
        rotation: steer_magnitude,
        ease: Sine.easeInOut
      })
      .to("#steering_wheel", steer_duration / 4, {
        rotation: 0,
        ease: Sine.easeInOut
      })
      .to("#steering_wheel", steer_duration / 4, {
        rotation: 0,
        ease: Sine.easeInOut
      });
    tl_steer
      .to(
        "#steer .bg",
        steer_duration / 4,
        { fill: response_color, ease: Sine.easeInOut },
        "start"
      )
      .to(
        "#steer .bg",
        steer_duration / 8,
        { fill: "#619a66", ease: Sine.easeInOut },
        "anchor1"
      )
      .to(
        "#steer .bg",
        steer_duration / 8,
        { fill: response_color, ease: Sine.easeInOut },
        "anchor2"
      )
      .to(
        "#steer .bg",
        steer_duration / 4,
        { fill: "#619a66", ease: Sine.easeInOut },
        "anchor3"
      );
    tl_steer.pause().progress(0);

    this.$options.timelines = {
      brake: tl_brake,
      steer: tl_steer
    };
  },

  methods: {
    displaySVGs: function() {
      const tl_forward = new TimelineMax();
      TweenMax.set(this.svg_target, { alpha: 1 });
      tl_forward.staggerFromTo(
        this.svg_target,
        0.5,
        {
          transformOrigin: "center",
          rotation: 180,
          scaleX: 0,
          scaleY: 0
        },
        { scaleX: 1, scaleY: 1, rotation: 0, ease: Back.easeOut.config(1.7) },
        0.1
      );
      tl_forward.add(this.$options.timelines["brake"].play());
      tl_forward.add(this.$options.timelines["steer"].play());
    },
    hideSVGs: function() {
      TweenMax.staggerTo(
        this.svg_target,
        1,
        {
          transformOrigin: "center",
          rotation: -180,
          scaleX: 0,
          scaleY: 0,
          alpha: 0,
          ease: Back.easeOut.config(5)
        },
        0.25
      );
    }
  }
};
</script>

<style scoped>
svg {
  width: 30%;
}
</style>
